import { memo, useEffect } from 'react';
import { useSelector } from 'react-redux';
import dynamic from 'next/dynamic';
import { useTourEventState } from '@containers/Home/global/TourStats';
import { useMobileDetect } from '@containers/Home/hook/useDetectMobile';
import { selectConfigMap, selectIsMatterportProject } from '../../selectors';
import { I_Scene } from '../../types';
import { MapWrapper } from './styled';

const Box = dynamic(() => import('./DisplayBox'));
const Panel = dynamic(() => import('./DisplayPanel'));
const PopupMobile = dynamic(() => import('./DisplayOnMobile'));

interface Props {
  onChangeScene: (scene: I_Scene) => void;
}

const Map = memo(({ onChangeScene }: Props) => {
  const { checkDevice } = useMobileDetect();
  const configMap = useSelector(selectConfigMap);
  const isMatterportProject = useSelector(selectIsMatterportProject);

  const [, setTourEventStats] = useTourEventState('state');

  useEffect(() => {
    setTourEventStats(prev => ({
      ...prev,
      isVisibleMap:
        (!checkDevice().isMobile() || isMatterportProject) &&
        !!configMap?.isShowOnStart,
    }));
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [configMap?.isShowOnStart]);

  return checkDevice().isMobile() || isMatterportProject ? (
    <PopupMobile onChangeScene={onChangeScene} />
  ) : (
    <MapWrapper>
      {configMap?.map?.type === 'box' ? (
        <Box onChangeScene={onChangeScene} />
      ) : (
        <Panel onChangeScene={onChangeScene} />
      )}
    </MapWrapper>
  );
});

export default Map;
